import { createGlobalStyle } from "styled-components";
import background from "./images/background.jpg";

export const GlobalStyle = createGlobalStyle`
* {
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
body {
    // background: ${({ theme }) => theme.body};
    // background: linear-gradient(${({ theme }) =>
      theme.body}) url(${background});
      // filter: brightness(50%);
      background: url(${background});
      background-color: rgb(0, 0, 0, 0.7);
    // background-image: url(${background})no-repeat center center fixed ;
    min-width: 100%;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
  }
  section{
  transition: background .3s ease;
}
`;

export const lightTheme = {
  //   body: "linear-gradient(180deg, hsl(232, 19%, 15%) 30%, hsl(230, 17%, 14%) 70%)",
  // body: "url('./images/background.jpg'), linear-gradient(#eb01a5, #d13531)",
  // body: "rgba(0, 0, 0, 0.7)",

  projects: "red",
  text: "#fff",
  toggle: "linear-gradient(hsl(210, 78%, 56%), hsl(146, 68%, 55%))",
  content: "hsl(228, 28%, 20%)",
  hover: "hsl(228, 28%, 40%)",
  icon: "#fff",
  navbar: "hsl(0, 0%, 40%)",
  navbarborder: "hsl(0,0%, 30%)"
};

export const darkTheme = {
  // body: "hsl(10, 0%, 100%)",
  // body: "url('./images/background.jpg')",
  // body: "rgba(0, 0, 0, 0.7)",
  projects: "yellow",
  text: "hsl(228, 12%, 44%)",
  toggle: "linear-gradient(hsl(210, 78%, 56%), hsl(146, 68%, 55%))",
  content: "hsl(227, 47%, 96%)",
  hover: "hsl(226, 47%, 90%)",
  icon: "black",
  navbar: "hsl(0, 0%, 80%)",
  navbarborder: "hsl(0,0%, 75%)"
};
